import { Button } from "@mui/material";
import { Link as ScrollLink } from "react-scroll";

export const HeaderButton = ({ href, children }) => {
  return (
    <div>
      <ScrollLink
        to={href}
        smooth={true} // 
        offset={-70} // 
        duration={1000} // ms
      >
        <Button key={children} sx={{ color: '#fff', fontSize: '16px' }}>
          {children}
        </Button>
      </ScrollLink>
    </div>
  );
};
